@use '../../foundation/mixin' as m;
@use '../../object/component/bubbly-button';

.p-category-nav {
	width: 230px;

	.side-title {
		height: 25px;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.side-title,
	.menu-category-nav-container ul li a {
		color: #fff;
		text-shadow: 1px 1px 3px #000630;
	}

	.menu-category-nav-container {
		padding: 5px 20px;
		.menu {
			li {
				padding: 5px 0;
				a {
					@extend .c-bubbly-button--yellow;
					font-size: 1.5rem;
					font-weight: 500;
				}
			}
			.sub-menu li a {
				margin-left: 20px;
				font-size: 1rem;
			}
		}
	}
}
