.c-anim-box {
	display: block;
	opacity: 0;
	transition: 2s;

	&--up,
	&--lr,
	&--rl,
	&--down,
	&--scaleup,
	&--scaledown,
	&--rotate-l,
	&--rotate-r {
		@extend .c-anim-box;
	}

	&--up {
		transform: translate(0, 50px);
	}
	&--lr {
		transform: translate(-200px, 0);
	}
	&--rl {
		transform: translate(200px, 0);
	}
	// &--down {
	// 	transform: translate(0, -200px);
	// }
	&--scaleup {
		transform: scale(0.5);
	}
	&--scaledown {
		transform: scale(1.5);
	}
	&--rotate-l {
		transform: rotate(180deg);
	}
	&--rotate-r {
		transform: rotate(-180deg);
	}
}

