@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

.wp-post-article-part {
	font-size: 1.5rem;
	line-height: 2;
	margin-bottom: 200px;

	ol,
	ul {
		padding-left: 20px;
		line-height: 1.8;
		li {
			ul {
				padding-left: 20px;
			}
		}
	}
}

.single {
	.l-main {
		.wp-post-article-part {
			h2 {
				width: 100%;
				margin-top: 80px;
				text-shadow: 1px 1px 3px #fff;

				&::before,
				&::after {
					top: inherit;
					bottom: 0;
				}
				&::before {
					height: 100%;
					background: linear-gradient(
						to left,
						rgba(0, 0, 0, 0.4),
						rgba(0, 0, 0, 0.3) 0%,
						rgba(0, 0, 0, 0.1) 85%
					);
				}
				&::after {
					height: 5%;
					background: #222;
				}
			}

			h3 {
				font-weight: 700;
				margin-bottom: 10px;
			}
		}
	}
}
