@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;
@use '../project/loop';

.p-frame {
	padding: 0 20px;
	display: flex;
	justify-content: center;

	.p-title,
	.related-title {
		margin-bottom: 20px;
	}

	.related-title {
		font-size: 1.5rem;
	}

	&--ex {
		@extend .p-frame;
		min-height: 50vh;

		.p-loop {
			@extend .p-loop;
			flex-direction: column;
			align-items: center;

			.post-sub-loop {
				margin-bottom: 40px;
			}
		}
	}
}

.single {
	.p-frame {
		margin-bottom: 80px;
		.p-loop {
			width: 100%;
			height: 100%;

			@include m.breakpoint(pc_mini) {
				width: 70%;
				max-width: 800px;
			}

			.post-sub-loop {
				width: 100%;
			}
		}
	}
}
