@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

.p-post-sub-loop {
	margin-bottom: 20px;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(310px, 310px));
	justify-content: center;
	justify-items: center;
	gap: 40px;

	.p-posts-list {
		&__item {
			a {
				width: 310px;
				height: 235px;

				display: grid;
				grid-template-rows: 78% 22%;
				place-items: center;
				align-content: flex-start;
				cursor: pointer;

				border-radius: 20px;
				opacity: 0.8;
				transition: 0.7s ease;

				@include m.breakpoint(moon_mini) {
					&:hover {
						transform: scale(1.1);
					}
				}
			}
		}
	}
}

.home,
.archive {
	.p-post-sub-loop {
		@include m.breakpoint(moon_mini) {
			width: 80vw;
			max-width: 1000px;
		}
	}
}
