@use '../../foundation/variable' as v;
@use '../../foundation/mixin' as m;
@use 'sass:math';

.p-ball-box {
	.c-ball {
		position: absolute;

		&:nth-child(1) {
			top: 0;
			left: -30%;
			width: 60px;
			height: 60px;
			@include m.breakpoint(sec_contact) {
				left: -10%;
			}
		}
		&:nth-child(2) {
			top: 0;
			right: -40%;
			width: 80px;
			height: 80px;
			z-index: 2;
			@include m.breakpoint(sec_contact) {
				right: -15%;
			}
		}
		&:nth-child(3) {
			bottom: 50%;
			left: -50%;
			width: 120px;
			height: 120px;
			z-index: 2;
			@include m.breakpoint(sec_contact) {
				left: -30%;
			}
		}
		&:nth-child(4) {
			bottom: 20%;
			right: -50%;
			width: 120px;
			height: 120px;
			z-index: 2;
			@include m.breakpoint(sec_contact) {
				right: -30%;
			}
		}
		&:nth-child(5) {
			bottom: -10%;
			left: -40%;
			width: 60px;
			height: 60px;
			@include m.breakpoint(sec_contact) {
				left: 20%;
			}
		}
	}
}
