.c-field-list {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	padding-left: 0;

	li {
		position: relative;
		white-space: nowrap;
		list-style: none;
		margin: 0;

		a {
			position: relative;
			display: block;
			z-index: 10;
		}

		&::after {
			position: absolute;
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			top: 0;
			border-radius: 20px;
			opacity: 0.4;
		}

		.text {
			line-height: 2;
			padding: 0 10px;
		}
	}

	&--black {
		@extend .c-field-list;
		margin: 5px 0 7px 0;
		li {
			&::after {
				background: linear-gradient(
					to right,
					white,
					rgba(255, 255, 255, 0.4) 0%,
					rgba(255, 255, 255, 0.1) 90%
				);
				border-right: 1px solid rgba(255, 255, 255, 0.4);
				border-bottom: 1px solid rgba(255, 255, 255, 0.2);
				box-shadow: inset 1px 1px 1px rgba(255, 255, 255, 0.2);
			}
			.text {
				font-size: 1.5rem;
				color: #fff;
				margin-bottom: 0;
			}
		}
	}

	&--white {
		@extend .c-field-list;
		li {
			&::after {
				background: linear-gradient(
					to right,
					rgb(0, 0, 0),
					rgba(255, 255, 255, 0.4) 0%,
					rgba(0, 0, 0, 0.2) 90%
				);
				border-right: 1px solid rgba(0, 0, 0, 0.4);
				border-bottom: 1px solid rgba(0, 0, 0, 0.2);
				box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.2);
			}
			.text {
				font-size: 1rem;
				color: #222;
			}
		}
	}
}
