@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

.p-contents {
	&--top,
	&--archive {
		.l-main__container {
			display: flex;
			flex-direction: column;
			gap: 20px;
		}
	}

	&--top {
		.l-main__container {
			@include m.breakpoint(pc_mini) {
				max-width: 800px;
			}
		}
	}

	&--archive {
		.l-main__container {
			width: 100%;
			padding-bottom: 100px;

			@include m.breakpoint(pc_mini) {
				width: 80%;
			}
		}
	}

	// postもworksも共通
	&--post {
		.l-main__container {
			width: 100%;
			margin-bottom: 0;

			@include m.breakpoint(pc_mini) {
				max-width: 800px;
				padding: 0;
			}

			.c-post-date {
				font-size: 1rem;
				margin-bottom: 10px;
			}
		}
	}

	&--fullscreen {
		height: 100vh;
		display: flex;
		flex-direction: column;

		.l-main__container {
			position: absolute;
			display: grid;
			gap: 10px;
			width: 80vw;
			height: 80vh;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			text-align: center;

			.p-title {
				margin: auto;
			}
		}
	}
}
