@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

// postとworks共通
.single {
	.l-main {
		// 関連記事もh2のためl-mainの中で設定
		h2 {
			position: relative;
			display: inline-block;
			font-size: 2rem;
			padding: 0 10px;
			overflow: hidden;

			&::before,
			&::after {
				position: absolute;
				top: 0;
				left: 0;
				display: block;
				content: '';
				width: 100%;
				height: 100%;
				border-radius: 5px;
				transform-origin: 0 0;
			}

			&::after {
				filter: blur(2px);
			}
		}

		.p-svg-cosmos {
			&--ornament {
				width: 2%;
				left: 5%;
				@include m.breakpoint(moon_mini) {
					left: 15%;
					opacity: 0.3;
				}
			}
		}

		&__container {
			.c-thumbnail {
				opacity: 0.7;
				margin-bottom: 40px;

				.svg-inline--fa,
				.fa-external-link-alt {
					position: absolute;
					bottom: 5px;
					left: 5px;
					font-size: 3rem;
					color: #9999;
					opacity: 0.8;
					z-index: 2;
				}

				.c-eye-catch-text {
					font-size: 6vw;
					margin-bottom: 0;
					@include m.breakpoint(moon_mini) {
						font-size: 3rem;
						line-height: 2;
					}
				}
			}
		}
	}
}

@keyframes title-after {
	100% {
		transform: translateX(0);
	}
}

// タグリスト
.single {
	.c-field-list--white {
		li {
			list-style: none;
			margin: 0;
		}
	}
}
