@use '../../foundation/variable' as v;
@use '../../foundation/mixin' as m;

.p-sec-contact {
	&__container {
		display: flex;
		justify-content: center;
	}

	&__inner {
		position: relative;
		width: 300px;
		border-radius: 10px;

		@include m.breakpoint(sec_contact) {
			width: 400px;
		}
	}

	.c-offset-title {
		margin-top: 100px;
	}
}
