.c-thumbnail {
	&,
	img {
		width: 100%;
		height: 100%;
	}

	position: relative;
	display: flex;
	justify-content: center;
	overflow: hidden;
	transition: 0.5s ease;

	img {
		display: block;
		border-radius: 20px;
		object-fit: cover;
	}

	.c-eye-catch-text {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}
