@mixin breakpoint($point) {
	@if $point == iphone_normal {
		@media (min-width: 374px) {
			@content;
		}
	}
	@if $point == moon_mini {
		@media (min-width: 570px) {
			@content;
		}
	}
	@if $point == tablet {
		@media (min-width: 600px) {
			@content;
		}
	}
	@if $point == header_large {
		@media (min-width: 740px) {
			@content;
		}
	}
	@if $point == back_to_moon_large {
		@media (min-width: 751px) {
			@content;
		}
	}
	@if $point == sec_contact {
		@media (min-width: 800px) {
			@content;
		}
	}
	@if $point == pc_mini {
		@media (min-width: 890px) {
			@content;
		}
	}
	@if $point == pc {
		@media (min-width: 960px) {
			@content;
		}
	}
	@if $point == wide {
		@media (min-width: 1120px) {
			@content;
		}
	}
}

// **
// line-heightの余白を取り除く
// **
@mixin lineHeightCrop-before($line-height) {
	&::before {
		display: block;
		width: 0;
		height: 0;
		margin-top: calc((1 - #{$line-height}) * 0.5em);
		content: '';
	}
}
@mixin lineHeightCrop-after($line-height) {
	&::after {
		display: block;
		width: 0;
		height: 0;
		margin-bottom: calc((1 - #{$line-height}) * 0.5em);
		content: '';
	}
}
