@use '../../foundation/variable' as v;
@use '../../foundation/mixin' as m;

// ロゴ非表示の設定
.grecaptcha-badge {
	visibility: hidden;
}

.wpcf7 {
	width: 100%;

	&-form {
		display: grid;
		gap: 40px;
	}

	p {
		font-size: v.$font-size--c-contact-table;
		font-weight: v.$font-weight--base;
		letter-spacing: 1px;
		color: #fff;
		display: block;

		&:nth-child(9) {
			font-size: 1rem;
		}

		label {
			padding-left: 20px;
		}
	}

	input,
	textarea {
		background: rgba(255, 255, 255, 0.2);
		border-right: 1px solid rgba(255, 255, 255, 0.2);
		border-bottom: 1px solid rgba(255, 255, 255, 0.2);
		box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
		border: 1px solid rgba(255, 255, 255, 0.5);

		margin-top: 3px;
		width: 100%;
		padding: 10px 20px;
		border-radius: 20px;
		outline: none;

		&::placeholder {
			color: transparent;
		}
	}

	input:focus,
	textarea:focus {
		background: rgba(0, 0, 0, 0.5);
	}

	input[type='text']:not(:placeholder-shown),
	textarea:not(:placeholder-shown) {
		background: rgba(255, 255, 255, 0.5);
		color: #222;
		font-weight: v.$font-weight--light;
	}

	input:invalid,
	textarea:invalid {
		background: rgba(255, 0, 0, 0.5);
	}

	input[type='submit'] {
		max-width: 100px;
		color: #666;
		font-weight: v.$font-weight--input-submit-btn;
		background: #fff;
		cursor: pointer;
		transition: 0.5s;

		&:hover {
			background: rgba(255, 255, 0, 0.5);
		}
	}
}

.wpcf7-not-valid-tip,
.wpcf7-response-output {
	color: blue;
	font-size: 2rem;
	font-weight: v.$font-weight--superBold;
}
