@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

.p-tax-list {
	@include m.breakpoint(moon_mini) {
		// width: 50vw;
		max-width: 700px;
	}

	display: flex;
	flex-direction: column;
	gap: 2px;
	margin-bottom: 20px;

	.p-posts-list {
		&__item,
		&__item--list {
			display: flex;
			justify-content: center;

			a {
				width: 100%;
				height: 100%;

				border-radius: 0;
			}
		}

		&__title {
			padding: 10px 20px;
			text-align: left;
			line-height: 1.7;
		}
	}
}
