@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

.p-posts-list {
	&,
	&__figure,
	&__figure img {
		border-radius: 20px 20px 0 0;
	}

	&__item {
		width: 100%;
	}

	&__body {
		width: 100%;
		height: 100%;

		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0 5px;
		text-align: left;
	}

	&__title {
		font-size: 1.5rem;
		font-weight: 500;
		color: #222;
		line-height: 1;
	}
}
