// *
// セクションの余白
// *
$gap--sec-top: 100px;
$gap--sec-bottom: 200px;

// *
// フォントサイズ
// *
$font-size--sec-ttl: 2.4rem;
$font-size--header-nav: 1.6rem; // ナビゲーションの文字
$font-size--hamburger-nav: 2rem; // ハンバーガーメニューの文字
$font-size--moon--sp: 2.5rem; // 月の中の文字
$font-size--moon--pc: min(3.5vmin, 2.5rem); // 月の中の文字

$font-size--card-ttl: 1.6rem;
$font-size--c-card-info--01: 1.5rem;
$font-size--c-card-info--02: 1.2rem;
$font-size--c-card-info--03: 1.3rem;

$font-size--profile-slide--ttl--sp: 4.2vw;
$font-size--profile-slide--ttl--pc: 2rem;
$font-size--profile-slide--txt--sp: 4vw;
$font-size--profile-slide--txt--pc: 1.6rem;
$font-size--profile-slide--pagination: 1.2rem;
$font-size--profile-slide--next-prev: 1.5rem;

$font-size--p-skills-ball: 1.5em;
$font-size--p-skills-ball--small: min(10px, 1.5em);

$font-size--c-contact-table: 1.6rem;
$font-size--p-offset-text: 2.5rem;

// *
// フォントウェイト
// *
$font-weight--base: 100; //指定しなければ100
$font-weight--light: 300;
$font-weight--regular: 400;
$font-weight--bold: 500;
$font-weight--superBold: 600;

// 見出し
$font-weight--h1: $font-weight--superBold;
$font-weight--h2: $font-weight--light;
$font-weight--h3: $font-weight--bold;

// $font-weight--light: 300;
$font-weight--card-ttl: $font-weight--light;
$font-weight--c-sec-ttl: $font-weight--light;
$font-weight--profile-slide--txt--sp: $font-weight--light;

// $font-weight--superBold: 600;
$font-weight--input-submit-btn: $font-weight--superBold;

// *
// ラインハイト
// *
$line-height--normal: 1.5;
$line-height--middle: 1.8;
$line-height--wide: 2;
$line-height--very-wide: 3;

// *
// カラー設定
// *
// デフォルト
$color--main: #fff;
$color--sub: rgb(255, 255, 85);
$color--accent: rgb(102, 0, 102);

$color--white: rgb(255, 255, 255);
$color--white--opacity10: rgba(255, 255, 255, 0.1);
$color--white--opacity20: rgba(255, 255, 255, 0.2);
$color--white--opacity30: rgba(255, 255, 255, 0.3);
$color--white--opacity40: rgba(255, 255, 255, 0.4);
$color--white--opacity50: rgba(255, 255, 255, 0.5);
$color--white--opacity60: rgba(255, 255, 255, 0.6);
$color--white--opacity80: rgba(255, 255, 255, 0.8);

$color--light-purple: rgb(210, 189, 255);
$color--light-purple--opacity90: rgba($color--light-purple, 0.9);
$color--light-green: rgb(200, 255, 230);
$color--light-green--opacity70: rgba($color--light-green, 0.7);
$color--pink--opacity80: rgba(255, 200, 200, 0.8);

// 月
$color--crater-waning: rgba(255, 140, 140, 0.5);
$color--moon-main: $color--sub;
$color--moon-waning: rgba($color--accent, 0.5);
// $color--moon-waning: rgba(102, 0, 102, 0.5);

$color--moon-waning-shadow: $color--sub;
$color--moon-shadow-start: rgba(255, 255, 1, 0.5);
$color--moon-shadow-middle: rgba(255, 140, 140, 0.5);

// コスモス色
$color--cosmos: linear-gradient(
	0deg,
	rgba(255, 255, 255, 0.1),
	rgba(255, 255, 255, 0.2),
	rgba(255, 255, 255, 0.3),
	rgba(255, 255, 255, 0.4)
);

// svg背景のカラー
$color--g-star--tiny: #ffee00;
$color--g-star--twinkle: rgb(255, 119, 28);
$color--g-saturn: #f7d3a4;
$color--g-saturn--ring: #fdd8d5;
$color--g-crystal: #efc87e;
$color--g-crystal--parts: #efa77e;
$color--g-circle: rgb(255, 255, 153);

// 泡ボタン
$color--bubbly-btn-white: $color--main;
$color--bubbly-btn-pink: $color--accent;
$color--bubbly-btn-yellow: $color--sub;
$color--bubbly-btn-text: #000;

// swiperボタン
// $color--profile-swiper-btn: #a3519b;
$color--profile-swiper-btn: rgba($color--accent, 0.4);

// ホバーのマーカー
$color--hover-marker--white: rgba(#fff, 0.2);
$color--hover-marker--yellow: rgba($color--sub, 0.2);

// *
// 背景色
// *
$bg-linear-light-before: linear-gradient(
	to right,
	rgba(255, 255, 255, 0.1) 10%,
	rgba(255, 255, 255, 0.4) 80%,
	rgb(255, 255, 255)
);

$bg-linear-light-before--post-sec-ttl: linear-gradient(
	to right,
	rgba(0, 0, 0, 0.1) 10%,
	rgba(0, 0, 0, 0.4) 80%,
	rgb(0, 0, 0)
);

// *
// フォントカラー&テキストシャドウ
// *
$font-color--moon-text: $color--sub; //月の中の文字

$font-color--card-ttl: #fff; //カードヘッダー

$font-color--skills--first: rgba(255, 255, 220, 0.8); //スキルfirst
$font-color--skills--child: rgba($color--sub, 0.7); //スキルその他
$text-shadow--skills--first: 1px 1px 4px rgba($color--accent, 0.7); //スキルfirstのテキストシャドウ
$text-shadow--skills--child: 1px 1px 2px rgba($color--accent, 0.7); //スキルfirstのテキストシャドウ

// *
// サイズと余白
// *

// 月サイズ
$size--moon_mini: 35vmin;
$size--moon-big: 80vw;
$position--moon_mini-top: 15vmin;
$position--moon-big-top: 15vmin;

// ハンバーガー
$size--hamburger-container: 40px; // ボタン全体の１辺の長さ
$position--hamburger-nav-top: 50px; //メニューのトップ位置
$gap--hamburger-nav: 80px; //メニューの縦gap

// ヘッダー
$gap--header--sp: 10px;
$gap--header--pc: 5vw;
$gap--header--wide: 8vw;
$gap--c-nav: 20px;
$gap--p-header-side--pc: 20px;
$width--nav-li: 77.75px;

// swiper(height:240px;)
$width--card--single: min(80vw, 420px);
$height--card--single: min(43vmin, 232px); // swiper（プロフィール）の高さ

//works
$width--p-card: 250px;
$height--p-card--header: 30px;

// linear-light
$width--linear-light--home: 300px;

// l-mainの内枠
$width--pc: 50%;

// first-view
$height--first-view--sp: 300px;
$height--first-view--pc: 350px;

// *
// タイム
// *
$time--hamburger-anim: 0.2s;

