@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

// worksのみの設定
.single-works {
	.l-main {
		.p-svg-cosmos {
			&--earth {
				top: 30%;
			}
			&--ornament {
				opacity: 0.4;
				// @include m.breakpoint( moon_mini ) {
				// }
			}
		}

		.l-main__container {
			margin-bottom: 300px;
			padding: 0 20px;

			@include m.breakpoint(pc_mini) {
				width: 50%;
			}

			.works-category-list {
				margin-bottom: 5px;
				.text {
					line-height: 1.4;
				}
			}

			.field-part {
				margin-bottom: 100px;
				&__contents {
					margin-bottom: 40px;
				}

				h2 {
					font-size: 1.5rem;
				}

				h2,
				p {
					margin-bottom: 0;
				}

				.product-url {
					display: block;
					color: #fff;
					&::before {
						font-family: 'Font Awesome 5 Free';
						content: '\f35d';
						font-weight: 900;
						padding-right: 10px;
						opacity: 0.5;
					}
				}
			}

			.c-falling-box {
				bottom: -5%;
			}
		}
	}
}

.works-category-list {
	gap: 3px;

	li {
		a {
			padding: 0 8px;
			&::after {
				border-radius: 8px;
			}
		}
	}
}

.single-works {
	h2 {
		&::before {
			background: linear-gradient(
				to right,
				rgb(255, 255, 255),
				rgba(255, 255, 255, 0.4) 0%,
				rgba(255, 255, 255, 0.1) 85%
			);
		}
		&::after {
			background: rgba(255, 255, 255, 0.2);
		}
	}
}
