@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

// worksに反映させない場合
.single-post {
	.p-svg-cosmos {
		width: 200%;
		@include m.breakpoint(moon_mini) {
			width: 100%;
		}
		&--earth {
			top: 15%;
			@include m.breakpoint(moon_mini) {
				top: 10%;
			}
		}
		&--ornament {
			top: 350px;
			z-index: 10;
			@include m.breakpoint(moon_mini) {
				top: 350px;
			}
		}
	}

	.l-main {
		font-family: sans-serif;

		&__inner {
			position: relative;
			padding-bottom: 50px;
			margin: -5px 0;

			@include m.breakpoint(moon_mini) {
				margin: -10px 0;
			}
		}

		&__container {
			color: #000; // worksは白なので、postだけ黒に

			@include m.breakpoint(moon_mini) {
				width: 70%;
			}

			li {
				list-style: inherit;
				overflow-wrap: break-word;
			}

			.c-breadcrumb ul li {
				margin-left: 0;
			}

			.c-pseudo--sec-ttl {
				&::before {
					background: v.$bg-linear-light-before--post-sec-ttl;
				}
			}
		}

		.c-separator-cloud__container,
		.c-separator-cloud--bottom {
			width: 200%;
			margin-left: -10px;
			@include m.breakpoint(moon_mini) {
				width: 100%;
				margin-left: 0;
			}
		}
	}
}
