@use '../../foundation/variable' as v;
@use '../../foundation/mixin' as m;
@use '../component/bubbly-button';

.c-pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 5px;

	li {
		position: relative;
		width: 35px;
		height: 35px;
		padding: 0;

		.page-numbers {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 100%;
			color: #fff;
			font-size: 2rem;
			font-weight: 300;

			&:not(.current) {
				@extend .c-bubbly-button--white;
				position: absolute;
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
				text-align: center;
				z-index: 50;
			}

			&.current {
				background: v.$color--cosmos;
				border-radius: 50%;
				font-size: 1rem;
			}
		}
	}

	.prev.page-numbers,
	.next.page-numbers {
		color: v.$color--white--opacity50;
	}

	.prev.page-numbers i,
	.next.page-numbers i {
		font-size: 20px;
	}
}
