@use '../../foundation/mixin' as m;
@use '../../foundation/variable' as v;

.c-ham-menu {
	position: absolute;
	top: 0;
	right: -110%;
	height: 100vh;
	text-align: left;
	background: rgba(0, 0, 0, 0.9);
	transition: 0.2s;
	z-index: 90;

	@include m.breakpoint(moon_mini) {
		position: static;
		width: inherit;
		height: inherit;
		background: none;
		text-align: center;
	}
}
